<template>
	<main class="container">
		<div id="div-h1">
			<h1>Associaci Cultural Marrinxa</h1>
		</div>

		<div id="txt-descripcio">
			<p>
				L'associaci Cutlural Marrinxa es va presentar el 16 d'abril del 2005 amb un acte a la Plaa de Santa Maria a Matar on es va retre homenatge a la figura d'Ovidi Montllor. L'acte va comptar amb la participaci de diferents collectius i grups artstics de diverses modalitats. Cadasc i a la seva manera van actuar per retre homenatge a l'obra del poeta valenci. Vam comptar amb la participaci de Joan Rovira al crrec d'una representaci teatral. Es va collocar una exposici de l'obra del poeta a la plaa durant tota la jornada i ens va acompanyar al sopar de Sardines i a les actuacions musicals que van portar-nos fins al final de l'acte. Els musics maresmencs que van donar inici a la nit musical van ser Whysboard Project. Seguidament, La Fera Ferotge, un grup composat per Cesk Freixas i alguns membres del grup ja dissolt Inadaptats van versionar algunes peces d'Ovidi Montllor. La Coixinera va tancar la nit amb la seva aposta musical d'arrel tradicional i ritmes del rock.
			</p>
			<p>
				Pels ms curiosos, aqu teniu la imatge del cartell, com veureu el disseny i lnia esttica de l'associaci ha anat evolucionant al llarg dels anys!
			</p>
		</div>
	</main>
</template>

<style scoped>
	#txt-descripcio{
		margin-top: 40px;
		background: #e8e1ce;
		padding: 20px;
		text-align: justify;
	}


</style>

